// 加载注册Vant组件模块

import Vue from 'vue'

// 加载
import {
  Button,
  Dialog,
  List,
  Image,
  Icon,
  NavBar,
  Field,
  CountDown,
  Toast,
  PullRefresh,
  Popup,
  Search,
  Loading,
  ActionSheet,
  DatetimePicker,
  ImagePreview,
  Row,
  Col,
  Cell,
  CellGroup,
  Tabbar,
  TabbarItem,
  Grid,
  GridItem,
  Tab,
  Tabs
} from 'vant'

// 注册
Vue.use(Button)
  .use(Dialog)
  .use(List)
  .use(Image)
  .use(Icon)
  .use(NavBar)
  .use(Field)
  .use(CountDown)
  .use(Toast)
  .use(PullRefresh)
  .use(Popup)
  .use(Search)
  .use(Loading)
  .use(ActionSheet)
  .use(DatetimePicker)
  .use(ImagePreview)
  .use(Row)
  .use(Col)
  .use(Cell)
  .use(CellGroup)
  .use(Tabbar)
  .use(TabbarItem)
  .use(Grid)
  .use(GridItem)
  .use(Tab)
  .use(Tabs)
